:root {

	/* Z-index levels */
}

.root {}

.inputWrapper {
	display: inline-block;
	position: relative;
}

/* custom input styles */

.trigger {
	background: #fff;
	border: 1px solid #424242;
	font-size: inherit;
	min-height: 25px;
	min-width: 200px;
	padding-right: 25px;
	position: relative;
	text-align: left;
}

.trigger i {
	position: absolute;
	right: 4px;
}

.trigger:focus {
	color: #34495e;
}

.placeholder {}

.required {}

.dropdown {
	background: #fff;
	border: 1px solid #424242;
	display: none;
	max-height: 400px;
	overflow-y: auto;
	position: absolute;
	width: 100%;
}

.open {}

.open .dropdown {
	display: block;
	z-index: 200;
}

/* native input styles */

.input {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	background: #fff;
	border: 1px solid #424242;
	border-radius: 0;
	cursor: pointer;
	display: block;
	font-size: inherit;
	line-height: 1;
	max-width: 100%;
	padding: 2px 25px 2px 4px;
	-webkit-transition: border .2s ease;
	transition: border .2s ease;
	width: 200px;
}

.input:focus {
	border-color: #34495e;
}

.arrow {
	bottom: 0;
	pointer-events: none;
	position: absolute;
	right: 0;
	top: 0;
	width: 1.5em;
}

.arrow i {
	position: absolute;
	right: 4px;
	padding: 2px 0;
}

.input:focus ~ .arrow {
	color: #34495e;
}

/* State classes */

.focused { }

.disabled .input,
.readonly .input {
	background-color: #bbb;
}

.invalid .input {
	border-color: red;
}

.invalid .arrow {
	background-color: red;
}

.required { }

.valid .input {
	border-color: green;
}

.valid .arrow {
	background-color: green;
}
